@charset "UTF-8"; /*! normalize.css v3.0.2 | MIT License | git.io/normalize */
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: 700;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: 700;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}

@import url('https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap');

$background: #05041d;
$black: #000;
$white: #fff;
$grey: #181d3c;
$purple: #2F22C8;
$gradient: linear-gradient(16deg, $grey 0, $background 100%);
$typepography: Space Mono, Courier;

* {
    box-sizing: border-box;
  }


.logo {
    display: flex;
    align-items: center;
    transform: translate(0px, 3px);

  img {
    transform: translateY(5px);
  }

    h2 {
        font-size: 1.4em;
        padding-left: 1.2em;
        
    }
}

header, body {
    background-color: $background;
    color: $white;
    font-family: $typepography;
    font-weight: 100;
}

.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 2rem auto;
    padding: 0 2rem;
}

.container-hero {
  margin: 2rem 2rem 14rem 2rem;
}

.container-hero img {
  display: flex;
  margin: 3rem auto 0rem auto;
  width: 100%;
}


a {
    color: $white;
}

nav a {
    margin-left: 30px;
    text-decoration: none;
    font-weight: 400;
}

h1 {
  font-family: $typepography;
  font-size: 8.125rem;
  font-style: normal;
  font-weight: 500;
  text-align: right;
  line-height: normal;
  letter-spacing: -0.325rem;
  align-items: flex-end;
}

h2 {
  font-weight: 400;
}

p {
  margin-top: 2rem;
  text-align: right;
  padding-left: 21rem;
}




div.post {

    p {
        max-width: 700px;
    }

    img {
        max-width: 1000px;
    }

}

nav a.selected {
 text-decoration: none;
 border-bottom: 5px solid $purple;
}

.contact {
  text-decoration: none;
  border-bottom: 5px solid $purple;
  font-size: 1.6rem;
}

.row {
display: -ms-flexbox; /* IE10 */
display: flex;
-ms-flex-wrap: wrap; /* IE10 */
flex-wrap: wrap;
// padding: 0 4px;
}

/* Create four equal columns that sit next to each other */
.column {
-ms-flex: 25%; /* IE10 */
flex: 25%;
max-width: 25%;
padding: 0 6px;
margin-bottom: 12px;
}

.column img {
margin-top: 12px;
vertical-align: middle;
width: 100%;
}



.blog-content {
display: block;
}


footer {
    padding: 25px;
    background-color: $grey;
    color: #fff;
    // height: 20rem;
  p {
      padding-left: 0;
    
    }
  }
  .footer-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 2rem;
  }
  .footer-nav {
    list-style: none;
    float: left;
    margin: -0.1em 3em 0 0;
    text-align: right;
    color: #fff;
    border-right: 1px solid;
    padding-right: 3em;
  }
  .footer-nav a:hover {
    border-bottom: 5px solid $purple;
    padding-bottom: 2px;
    transition: padding-bottom 0.3s;
    padding-left: initial;
  }
  .footer-nav * {
    margin-bottom: 0.5rem;
    padding-top: 0.2rem;
    transform: translateY(2px);
  }
  footer span {
    color: #fff;
    font-weight: 500;
  }
  #copyright {
    // max-width: 1200px;
    margin: 0 auto;
    font-size: 0.7rem;
    text-align: center;
  }
  .ext-links {
    display: flex;
    align-items: center;
  }
  footer p {
    padding-top: 2px;
  }
  .ext-links a {
    text-decoration: none;
    color: #fff;
    margin: 0 0.6rem;
  }

  #bd-email {
    color: #666b7a;
    height: 2.5rem;
    width: 18em;
    margin-bottom: 0.7rem;
    border-radius: 12px;
    border-style: none;
    padding-left: 1em;
  }

  .button {
    background-color: $purple;
    box-shadow: 0 2px 2px 0 rgba(17, 64, 87, 0.1),
      0 10px 20px 0 rgba(3, 48, 29, 0.09);
    font-family: "Work Sans", Helvectia, Arial, sans-serif;
    text-decoration: none;
    font-weight: 400;
    font-size: 0.9em;
    letter-spacing: 1px;
    color: #fff;
    padding: 1rem 2em;
    border-radius: 12px;
    text-align: center;
    display: inline-block;
    transition-duration: 0.3s;
    margin: 0 auto;
    width: 30%;
    max-width: 18em;
    min-width: 13em;
    border-style: none;
  }
  .button:hover {
    transform: scale(1.07, 1.07);
  }

  .ext-links img:hover {
    filter: brightness(95%);
    transform: scale(1.2);
    transition-duration: 0.3s;
  }
  .button:active {
    box-shadow: inset 0 2px 5px 0 rgba(0, 0, 0, 0.3);
  }

  .email-list .button {
    display: list-item;
    width: 20em;
    height: 2.5rem;
    max-width: initial;
    padding: initial;
    margin: initial;
  }

  .email-list {
    text-align: left;
    display: inline-block;
    margin: 3.75rem 1.2em 3.75rem 0;
  }
  .email-list h6 {
    font-size: 1.3em;
    color: #fff;
    font-weight: 500;
    margin: initial;
  }
  .email-list p {
    margin: 0 0 0.8rem 0;
    text-align: left;
  }
  label {
    color: #7e8a9a;
    margin-bottom: 0.25em;
  }


  #buttondown {
    font-size: 0.7rem;
    text-decoration: none;
  }
  .rule {
    display: none;
    border: 1px solid #fff;
    margin: 90px auto;
    max-width: 450px;
  }
  .mobile {
    display: none;
  }

  

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 900px) {

  .logo {
  
    h2 {
        display: none;
        
    }
  }
  .column {
    -ms-flex: 50%;
    flex: 50%;
    max-width: 50%;
  }
  .footer-content {
    flex-direction: column;
    flex-wrap: wrap;
    max-width: 770px;
  }
  

  footer {
  height: initial;
  }

  .email-list .button {
   margin: 0 auto;
    }

  #copyright {
    margin: 3rem auto 0 auto;
    text-align: center;
  }
  .button {
    width: 50vw;
    min-width: 10em;
  }
  .social {
    display: flex;
    align-items: center;
    margin: 1rem auto;
  }
  .social img {
    width: 2.5rem;
    margin: 0 .4rem;
  }
  .email-list {
    margin: 3.75rem auto 2rem auto;
    text-align: center;
    h6 {
      text-align: center;
    }
  }

  .ext-links {
    transform: translateX(0);
    display: flex;
    flex-flow: wrap;
    margin-top: 8px;
    // margin: 1rem auto;
    font-size: 1.4rem;
  }
  .footer-nav {
    margin: 1rem auto 2.3rem auto;
    text-align: center;
    color: #fff;
    border: initial;
    padding: 0;
    width: 50vw;
  }
  .emailWidth img {
    width: 375px;
  }    
  
p {
  text-align: right;
  padding-left: 2rem;
}

}


  
  

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
.column {
    -ms-flex: 100%;
    flex: 100%;
    max-width: 100%;
    margin-bottom: 0px;
}

.container {
  padding: 0 1.2rem;
}

.container-hero {
  margin: 2rem 1rem 14rem 1rem;
}


.logo {
  
  h2 {
      display: none;
      
  }
}

h1 {
  font-size: 5rem;
  margin: 0 0 1rem 0;
}

p {
  margin-left: 1rem;
  padding-left: 1rem;
}
.email-list p {
  text-align: center;
}

.contact {
  font-size: 1.3rem;
}


}